import "bootstrap/dist/css/bootstrap.min.css";
import React, {Component} from 'react';
import '../scss/custom.scss'

import axios from 'axios';

//react bootstrap imports
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Form from "react-bootstrap/Form";

export default class CreateAccount extends Component {
    constructor(props) {
        super(props)

        this.onChangeName = this.onChangeName.bind(this)
        this.onChangeEmail = this.onChangeEmail.bind(this)
        this.onSubmit = this.onSubmit.bind(this)

        this.state = {
            name: '',
            email: '',
            valid: false
        }
    }
    onChangeName(e) {
        this.setState({
            name: e.target.value
        });
    }
    onChangeEmail(e) {
        this.setState({
            email: e.target.value
        });
    }
    onSubmit(e) {
        // e.preventDefault();
        const form = e.currentTarget;
        this.setState({valid: true})
        if (form.checkValidity() === false) {
            e.preventDefault();
            e.stopPropagation();
        } else {
            const newAccount = {
                name: this.state.name,
                email: this.state.email
            };
            axios.post('https://alertigo-api.herokuapp.com/accounts/add', newAccount)
                .then(res => {
                    this.setState({name: '', email: ''})
                });
            e.preventDefault()
        }
    }
    render() {
        return (
            <Container className='text-center text-dark pt-5 pb-5'>
                <h1 className='mb-5'>CREATE AN ACCOUNT</h1>
                <Form noValidate validated={this.state.valid} onSubmit={this.onSubmit}>
                    <Row className='justify-content-center'>
                        <Form.Group as={Col} xs={6} md={4} lg={3}>
                            <Form.Control 
                                value={this.state.name}
                                onChange={this.onChangeName}
                                placeholder="NAME"
                                className='bg-primary border-0 shadow'
                                required
                                    />
                        </Form.Group>
                        <Form.Group as={Col} xs={6} md={4} lg={3}>
                            <Form.Control 
                                value={this.state.email}
                                onChange={this.onChangeEmail}
                                placeholder="EMAIL"
                                className='bg-primary border-0 shadow'
                                required
                                    />
                        </Form.Group>
                    </Row>
                    <Row className='justify-content-center'>
                        <Form.Group className='mb-0 mt-3'>
                            <Button type='submit' className='text-dark shadow' variant="primary" >Submit</Button>
                        </Form.Group>
                    </Row>
                </Form>
            </Container>
        )
    }
}