import "bootstrap/dist/css/bootstrap.min.css";
import React, {Component, useContext} from 'react';
import { Link } from 'react-router-dom';
import '../scss/custom.scss'

import axios from 'axios';

//react bootstrap imports
import Accordion from "react-bootstrap/Accordion";
import AccordionContext from "react-bootstrap/AccordionContext";
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'
import CardColumns from 'react-bootstrap/CardColumns'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'
import Modal from 'react-bootstrap/Modal'
import Form from 'react-bootstrap/Form'

const formatDate = (time) => {
    // console.log(time)
    let [year, month, date] = time.split("-")
    let newDate = date.substring(0, 2)
    let newYear = year.substring(2, 4)
    return month+'/'+newDate+'/'+newYear
}

const formatTime = (time) => {
    let offset = (new Date().getTimezoneOffset())/60
    let timeStr = time.split("T")
    let hours = timeStr[1].substring(0,2) - offset
    let mins = timeStr[1].substring(3,5)
    let ampm = ''
    if(hours>12) {
        hours-=12
        ampm="PM"
    } else if (hours===0) {
        hours=12
        ampm="AM"
    } else if(hours<0) {
        hours = 12+hours
        ampm="PM"
    } else {
        ampm="AM"
    }
    return hours + ':' + mins + ampm
}

function ContextAwareToggle({ children, eventKey }) {
    const currentEventKey = useContext(AccordionContext);
    const isCurrentEventKey = currentEventKey === eventKey;

    return (
        <Accordion.Toggle 
            style={{'borderBottomRightRadius': isCurrentEventKey ? '0' : '', 'borderBottomLeftRadius': isCurrentEventKey ? '0' : '' }}
            block className='text-dark' 
            as={Button}  variant='primary' 
            eventKey={eventKey}>
            {children}
        </Accordion.Toggle>
    );
}

function NewAlertCard({children, showMe}) {
    if(showMe) {
        return (
            <Card 
                as={Col} xs={12} lg={4} 
                bg='primary' text='dark' 
                className='shadow border-0 text-center align-items-center
                 pl-0 pr-0 ml-auto mr-auto mt-4 mb-4'>
                {children}
            </Card>
        )
    } else return ''
}

export default class AlertsList extends Component {
    constructor(props){
        super(props);

        this.onChangeTicker = this.onChangeTicker.bind(this);
        this.onChangeStance = this.onChangeStance.bind(this);
        this.onChangeEntry = this.onChangeEntry.bind(this);
        this.onChangeExit = this.onChangeExit.bind(this);
        this.onChangeSynopsis = this.onChangeSynopsis.bind(this);
        this.onChangeAuthor = this.onChangeAuthor.bind(this);
        this.onSubmitCreate = this.onSubmitCreate.bind(this);
        this.onSubmitEdit = this.onSubmitEdit.bind(this);
        this.onChangePostDiscord = this.onChangePostDiscord.bind(this);

        this.state = {
            alerts: [],
            ticker: '',
            stance: 'true',
            entry: '',
            exit: '',
            synopsis: '',
            author: '',
            postDiscord: false,
            valid: false,
            showCreateModal:false,
            showEditModal:false,
            currentId: 0,
            admin: false || props.admin,
            alertType: props.alertType
        }
    }
    
    onChangeTicker(e) {
        this.setState({
            ticker: e.target.value
        });
    }
    onChangeStance(e) {
        this.setState({
            stance: e.target.value
        });
    }
    onChangeEntry(e) {
        this.setState({
            entry: e.target.value
        });
    }
    onChangeExit(e) {
        this.setState({
            exit: e.target.value
        });
    }
    onChangeSynopsis(e) {
        this.setState({
            synopsis: e.target.value
        });
    }
    onChangeAuthor(e) {
        this.setState({
            author: e.target.value
        });
    }
    onChangePostDiscord(e) {
        this.setState({
            postDiscord: !this.state.postDiscord
        });
    }

    onSubmitCreate(e) {
        // e.preventDefault();
        const form = e.currentTarget;
        this.setState({valid: true})
        if (form.checkValidity() === false) {
            e.preventDefault();
            e.stopPropagation();
        } else {
            let stanceBool = true;
            if(this.state.stance === 'true') stanceBool = true;
            else stanceBool = false;

            const newAlert = {
                ticker: this.state.ticker,
                stance: stanceBool,
                entry: this.state.entry,
                exit: this.state.exit,
                synopsis: this.state.synopsis,
                time: new Date(),
                author: this.state.author,
                postDiscord: this.state.postDiscord
            };
            axios.post('https://alertigo-api.herokuapp.com/'+ this.state.alertType + 'Alerts/add', newAlert)
                .then(res => {
                    this.setState({showCreateModal: false})
                    this.updateAlerts()
                });
            this.setState({
                ticker: '',
                stance: false,
                entry: '',
                exit: '',
                synopsis: '',
                author: '',
                postDiscord: false,
                valid: false
            })
            e.preventDefault()
        }
    }

    onSubmitEdit(e) {
        let url = window.location.href
        let id = url.substring(url.lastIndexOf('/') + 1);
        
        const form = e.currentTarget;
        this.setState({valid: true})
        if (form.checkValidity() === false) {
            e.preventDefault();
            e.stopPropagation();
        } else {
            let stanceBool = true;
            if(this.state.stance === 'true') stanceBool = true;
            else stanceBool = false;

            const newAlert = {
                ticker: this.state.ticker,
                stance: stanceBool,
                entry: this.state.entry,
                exit: this.state.exit,
                synopsis: this.state.synopsis,
                time: new Date(),
                author: this.state.author,
                postDiscord: this.state.postDiscord
            };
            axios.post('https://alertigo-api.herokuapp.com/' + this.state.alertType + 'Alerts/update/' + id, newAlert)
                .then(res => {
                    this.setState({showEditModal: false})
                    this.updateAlerts()
                });
            this.setState({
                ticker: '',
                stance: false,
                entry: '',
                exit: '',
                synopsis: '',
                author: '',
                postDiscord: false,
                valid: false
            })
            e.preventDefault()

        }
    }

    updateAlerts() {
        axios.get('https://alertigo-api.herokuapp.com/' + this.state.alertType + 'Alerts')
            .then(response => {
                this.setState({alerts: response.data.reverse()});
            })
            .catch((error) => {
                console.log(error);
            })
    }

    componentDidMount() {
        this.updateAlerts()
    }

    alertList() {
        let val=0    
        return this.state.alerts.map((currentAlert, i) => {
            val++;
            return (
                <Card key={val} 
                bg='primary' text='dark' 
                className='shadow border-0 mt-4 mb-4'>
                    <ContextAwareToggle eventKey={val}>
                        <h2 className='mb-0 text-uppercase'>{currentAlert.ticker}</h2>
                        <h5 className='mb-1 text-uppercase'>{(currentAlert.stance ? 'BULLISH' : 'BEARISH')}</h5>
                        <h6 className='mb-0 text-uppercase'>ENTRY TARGET: {currentAlert.entry}</h6>
                        <h6 xs={12} className='mb-0 text-uppercase'>EXIT TARGET: {currentAlert.exit}</h6>
                        <small>LAST UPDATE: {formatTime(currentAlert.time)} </small>
                    </ContextAwareToggle>
                    <Accordion.Collapse eventKey={val}>
                        <Card.Body className='p-0 text-center text-dark border-secondary border-top'>
                            <p style={{display: currentAlert.synopsis === '' ? 'none' : 'block'}} 
                            className='mt-3 pl-3 pr-3'>
                                {currentAlert.synopsis}
                            </p>
                            <Card.Footer className='border-0'>
                                <small className="text-dark text-muted mt-3">
                                    Posted by {currentAlert.author} on {formatDate(currentAlert.time)}
                                </small><br/>
                                {(this.state.admin) ? 
                                    <Link to={'/' + this.state.alertType + 'Alerts/' + currentAlert._id}>
                                        <Button className='shadow text-dark mt-2 mb-2 ml-3' 
                                        variant='secondary' onClick={this.showEditModal}>
                                            Edit Alert
                                        </Button>
                                    </Link>
                                : ''}
                            </Card.Footer>
                        </Card.Body>
                    </Accordion.Collapse>
                </Card>
            )
        })
    }

    showCreateModal = () => {this.setState({showCreateModal: true})}
    closeCreateModal = () => {this.setState({showCreateModal: false})}
    showEditModal = () => {
        setTimeout(() => {
            let url = window.location.href
            let id = url.substring(url.lastIndexOf('/') + 1);

            axios.get('https://alertigo-api.herokuapp.com/' + this.state.alertType + 'Alerts/'+id)
                .then(res => {
                    this.setState({
                        ticker: res.data.ticker,
                        entry: res.data.entry,
                        exit: res.data.exit,
                        author: res.data.author,
                        stance: res.data.stance ? 'true' : 'false',
                        synopsis: res.data.synopsis,
                        postDiscord: res.data.postDiscord
                    }, () => {
                        this.setState({showEditModal: true})
                    })
                })
        }, 1)
    }
    closeEditModal = () => {this.setState({showEditModal: false})}

    deleteAlert = () => {
        let url = window.location.href
        let id = url.substring(url.lastIndexOf('/') + 1);

        axios.post('https://alertigo-api.herokuapp.com/' + this.state.alertType + 'Alerts/remove/'+id)
            .then(res => {
                this.setState({showEditModal: false})
                this.updateAlerts()
            })
    }

    render() {
        return (
            <Container className='pt-4 pb-4'>
                <Modal size='lg' centered show={this.state.showCreateModal} onHide={this.closeCreateModal}>
                    <Modal.Header className='bg-primary border-0 text-dark' closeButton>
                        <Modal.Title >POST A NEW ALERT</Modal.Title>
                    </Modal.Header>
                    <Modal.Body className='bg-secondary rounded-bottom' >
                        <Form noValidate validated={this.state.valid} onSubmit={this.onSubmitCreate}>
                            <Row className='text-center'>
                                <Form.Group as={Col} xs={6} lg={3} controlId="ticker">
                                    <Form.Control 
                                        value={this.state.ticker}
                                        onChange={this.onChangeTicker}
                                        placeholder="TICKER"
                                        className='bg-primary border-0 shadow'
                                        required
                                            />
                                    {/* <Form.Text className='text-dark'>
                                        For stock options, write your contract here. Example: "AMZN 04/19 1000C"
                                    </Form.Text> */}
                                </Form.Group>
                                <Form.Group as={Col} xs={6} lg={3} controlId="entry">
                                    <Form.Control 
                                        value={this.state.entry}
                                        onChange={this.onChangeEntry}
                                        placeholder="ENTRY"
                                        className='bg-primary border-0 shadow'
                                        required
                                            />
                                    {/* <Form.Text className='text-dark'>
                                        Doesn't have to be a number. "Below 100" or "Above 400" works
                                    </Form.Text> */}
                                </Form.Group>
                                <Form.Group as={Col} xs={6} lg={3} controlId="exit">
                                    <Form.Control 
                                        value={this.state.exit}
                                        onChange={this.onChangeExit}
                                        placeholder="EXIT"
                                        className='bg-primary border-0 shadow'
                                        required
                                            />
                                    {/* <Form.Text className='text-dark'>
                                        Doesnt have to be a number. "ATH" works
                                    </Form.Text> */}
                                </Form.Group>
                                <Form.Group as={Col} xs={6} lg={3} controlId="author">
                                    <Form.Control 
                                        value={this.state.author}
                                        onChange={this.onChangeAuthor}
                                        placeholder="AUTHOR"
                                        className='bg-primary border-0 shadow'
                                        required 
                                        />
                                    {/* <Form.Text className='text-dark'>
                                        Capitalize the first letter. It'll look better
                                    </Form.Text> */}
                                </Form.Group>
                            </Row>
                            <Row className='text-center'>
                                <Form.Group as={Col} xs={6} required controlId="stance">
                                    <Form.Check inline 
                                        type="radio" 
                                        value='true'
                                        checked={this.state.stance === 'true'} 
                                        onChange={this.onChangeStance}
                                        label="BULLISH"
                                        />
                                    <Form.Check inline 
                                        type="radio" 
                                        value='false'
                                        checked={this.state.stance === 'false'} 
                                        onChange={this.onChangeStance}
                                        label="BEARISH"
                                        />
                                </Form.Group>
                                <Form.Group as={Col} xs={6}>
                                    <Form.Check 
                                        onChange={this.onChangePostDiscord}
                                        label="Post to Discord?"
                                        />
                                </Form.Group>
                            </Row>
                            <Row className='justify-content-center'>
                                <Form.Group as={Col} xs={12} sm={10} lg={11} controlId="synopsis">
                                    <Form.Control as='textarea'
                                        rows='2'
                                        value={this.state.synopsis}
                                        onChange={this.onChangeSynopsis}
                                        placeholder="SYNOPSIS (OPTIONAL)"
                                        className='bg-primary border-0 shadow'/>
                                </Form.Group>
                                <Form.Group className='mb-0'>
                                    <Button type='submit' className='text-dark shadow' variant="primary" >Submit</Button>
                                </Form.Group>
                            </Row>
                        </Form>
                    </Modal.Body>
                    {/* <Modal.Footer className='bg-primary'>
                        <Button onClick={this.deleteAlert} variant='secondary' className='text-dark'>Delete</Button>
                    </Modal.Footer> */}
                </Modal>
                <Modal size='lg' centered show={this.state.showEditModal} onHide={this.closeEditModal}>
                    <Modal.Header className='bg-primary border-0 text-dark' closeButton>
                        <Modal.Title>EDIT AN ALERT</Modal.Title>
                    </Modal.Header>
                    <Modal.Body className='bg-secondary rounded-bottom'>
                        <Form noValidate validated={this.state.valid} onSubmit={this.onSubmitEdit}>
                            <Row className='text-center'>
                                <Form.Group as={Col} xs={6} lg={3} controlId="ticker">
                                    <Form.Control 
                                        value={this.state.ticker}
                                        onChange={this.onChangeTicker}
                                        placeholder="TICKER"
                                        className='bg-primary border-0 shadow'
                                        required
                                            />
                                    {/* <Form.Text className='text-dark'>
                                        For stock options, write your contract here. Example: "AMZN 04/19 1000C"
                                    </Form.Text> */}
                                </Form.Group>
                                <Form.Group as={Col} xs={6} lg={3} controlId="entry">
                                    <Form.Control 
                                        value={this.state.entry}
                                        onChange={this.onChangeEntry}
                                        placeholder="ENTRY"
                                        className='bg-primary border-0 shadow'
                                        required
                                            />
                                    {/* <Form.Text className='text-dark'>
                                        Doesn't have to be a number. "Below 100" or "Above 400" works
                                    </Form.Text> */}
                                </Form.Group>
                                <Form.Group as={Col} xs={6} lg={3} controlId="exit">
                                    <Form.Control 
                                        value={this.state.exit}
                                        onChange={this.onChangeExit}
                                        placeholder="EXIT"
                                        className='bg-primary border-0 shadow'
                                        required
                                            />
                                    {/* <Form.Text className='text-dark'>
                                        Doesnt have to be a number. "ATH" works
                                    </Form.Text> */}
                                </Form.Group>
                                <Form.Group as={Col} xs={6} lg={3} controlId="author">
                                    <Form.Control 
                                        value={this.state.author}
                                        onChange={this.onChangeAuthor}
                                        placeholder="AUTHOR"
                                        className='bg-primary border-0 shadow'
                                        required 
                                        />
                                    {/* <Form.Text className='text-dark'>
                                        Capitalize the first letter. It'll look better
                                    </Form.Text> */}
                                </Form.Group>
                            </Row>
                            <Row className='text-center'>
                                <Form.Group as={Col} xs={6} required controlId="stance">
                                    <Form.Check inline 
                                        type="radio" 
                                        value='true'
                                        checked={this.state.stance === 'true'} 
                                        onChange={this.onChangeStance}
                                        label="BULLISH"
                                        />
                                    <Form.Check inline 
                                        type="radio" 
                                        value='false'
                                        checked={this.state.stance === 'false'} 
                                        onChange={this.onChangeStance}
                                        label="BEARISH"
                                        />
                                </Form.Group>
                                <Form.Group as={Col} xs={6}>
                                    <Form.Check 
                                        value={this.state.postDiscord}
                                        // checked={this.state.postDiscord} 
                                        onChange={this.onChangePostDiscord}
                                        label="Post to Discord?"
                                        />
                                </Form.Group>
                            </Row>
                            <Row className='justify-content-center'>
                                <Form.Group as={Col} xs={12} lg={11} controlId="synopsis">
                                    <Form.Control as='textarea'
                                        rows='2'
                                        value={this.state.synopsis}
                                        onChange={this.onChangeSynopsis}
                                        placeholder="SYNOPSIS (OPTIONAL)"
                                        className='bg-primary border-0 shadow'/>
                                </Form.Group>
                                <Form.Group xs={6} as={Col} className='mb-0'>
                                    <Button type='submit' className='text-dark shadow' variant="primary" >Submit</Button> 
                                </Form.Group>
                                <Link xs={6} to='/alerts/' onClick={this.deleteAlert} as={Col}><Button variant='primary' className='text-dark'>Delete</Button></Link>
                            </Row>
                        </Form>
                    </Modal.Body>
                </Modal>
                <NewAlertCard showMe={this.state.admin}>
                    <Button className='pt-5 pb-5 w-100 text-dark' onClick={this.showCreateModal}><h2 className='mt-1 mb-2'>NEW ALERT</h2></Button>
                </NewAlertCard>
                <Accordion>
                    <CardColumns>
                        {this.alertList()}
                    </CardColumns>
                </Accordion>
            </Container>  
        )
    }
}